import React, { Component } from 'react'
import { Link } from 'gatsby'
import Img from 'gatsby-image'

import { H2, Button } from '@style/global'
import styled from 'styled-components'
import theme from '@style/theme'
import { IconExternal } from 'components/Icon/IconExternal'

class Preview extends Component {
  render() {
    const listCategory = this.props.category.map((item, i) => {
      return <LsCat key={i}>{item}</LsCat>
    })
    const listRole = this.props.role.map((item, i) => {
      return <LsRole key={i}>{item}</LsRole>
    })
    return (
        <PreviewBloc>
            <Info>
              <CategoryDate>
                {listCategory}
                <time dateTime={this.props.date}>{this.props.date}</time>
              </CategoryDate>
              <H2>{this.props.title}</H2>
              <Desc>{this.props.desc}</Desc>
              <hr />
              <p>
                <TitleRole>Roles</TitleRole>
                {listRole}
              </p>
              <BtnGroup>
                {this.props.visit ? (
                  <Link to={this.props.visit} target="blank">
                    <Button Light>Visit Work　<IconExternal/></Button>
                  </Link>
                ) : (
                  ''
                )}
                {this.props.hideStory ? (
                  ''
                ) : (
                  <Link to={this.props.path}>
                    <Button tertiaryLight>Read Story</Button>
                  </Link>
                )}
              </BtnGroup>
            </Info>
            <ImgSlide>
              {this.props.img ? <Img fluid={this.props.img} /> : ''}
            </ImgSlide>
        </PreviewBloc>
    )
  }
}

export default Preview

const PreviewBloc = styled.div`
  display: grid;
  height: 100% !important;
  grid-template-columns: 2fr 3fr;
  padding: 0 !important;
  margin: 0 !important;
  @media (max-width: ${theme.screen.lg}) {
    grid-template-columns: unset !important;
    height: auto !important;
  }
`

const Info = styled.div`
  padding: 60px 140px 50px 0px;
  height: 100% !important;
  overflow: scroll;
  color: ${theme.color.primary.S100D};
  @media (max-width: ${theme.screen.lg}) {
    padding: 25px 16px 0 16px;
  }
  ${H2} {
    font-weight: bold;
  }
`

const ImgSlide = styled.div`
  @media (max-width: ${theme.screen.lg}) {
    order: -1;
    height: 200px;
    object-fit: cover;
  }
  div {
    height: 100% !important;
  }
  img {
    object-fit: cover;
    object-position: 50% 50%;
  }
`

const CategoryDate = styled.p`
  color: ${theme.color.primary.S600D};
  margin-bottom: 20px;
  font-family: ${theme.font.primary};
  ${theme.fontSize.xs};
  @media (max-width: ${theme.screen.lg}) {
    display: none;
  }
`

const LsCat = styled.span`
  ::after {
    content: ',   ';
  }
  :last-of-type {
    ::after {
      content: '・';
    }
  }
`

const Desc = styled.p`
  @media (max-width: ${theme.screen.lg}) {
    ${theme.fontSize.sm};
  }
`
const TitleRole = styled.div`
  font-family: ${theme.font.primary};
  ${theme.fontSize.xs};
  font-weight: bold;
  margin: 5px 0;
`
const LsRole = styled.span`
  ${theme.fontSize.sm};
  @media (max-width: ${theme.screen.lg}) {
    ${theme.fontSize.sm};
  }
  ::after {
    content: ' / ';
  }
  :last-of-type::after {
    content: '';
  }
`

const BtnGroup = styled.div`
  margin: 35px 0;
  @media (max-width: ${theme.screen.lg}) {
    margin: 25px 0;
  }
  div {
    margin-left: 10px;
  }
  div:first-of-type {
    margin-left: 0px !important;
  }
`
